.hero {
  // .main__info

  min-height: 100vh;
  @include tablet {
    height: auto;
  }

  &__content {
    position: relative;
    transition: all 0.5s ease;
    display: grid;
    grid-template-columns: 597px 1fr;
    min-height: 100vh;
    @include big-desktop {
      grid-template-columns: 547px 1fr;
    }
    &.project-visible {
      grid-template-columns: 0px 1fr;
      .swiper-nav {
        button.home-back {
          animation: btnVisible 0.5s forwards;
        }
      }
      .hero__project-upper {
        display: flex;
      }
      .tabs__nav {
        transform: translateX(140px);
      }
      .hero__tabs-upper {
      }
      .hero__info {
        transform: translateX(-100%);
        opacity: 0;
        animation: borderRightOut 0.1s;
      }
      .home-back.visible {
        opacity: 1;
        pointer-events: all;
      }
      .hero__info-container {
        min-width: 436px;
      }
      .logo {
        visibility: hidden;
      }
      .logo.visible {
        display: block;
      }
      .logo.project {
        display: block;
        visibility: visible;
        opacity: 1;
      }
      .info-graph-swiper {
        display: none;
      }
      .hero__tabs {
        width: 100%;
      }

      .footer {
        animation: footerOut 0.3s forwards;
      }
      .info-graph-projects-swiper {
        visibility: visible;
        position: relative;
        left: 0;
      }
      .logo.project.mobile {
        display: block;
      }
    }
    @include tablet {
      height: auto;
      display: grid;
      grid-template-columns: 1fr;
    }
    .hero__info-container:not(.mobile),
    .hero__model-container:not(.mobile) {
      position: absolute;
      top: 50%;
      display: flex;
      width: 100%;
      transform: translateY(-50%);
      transition: none;
    }

    &:has(.info-graph-button.animate) {
      @media (min-width: 1025px) {
        .hero__info-container:not(.mobile) {
          opacity: 0;
          animation: titleTo 0.25s ease-in forwards;
          pointer-events: all;
          animation-delay: 0.25s;
        }
        .hero__model-container:not(.mobile) {
          animation: titleOut 0.5s ease-in forwards;
          pointer-events: none;
        }
        .tabs__panel {
          &.first .swiper-wrapper {
            opacity: 0;
            animation: slideTo 0.25s ease-in forwards;
            animation-delay: 0.25s;
          }
          &.second .swiper-wrapper {
            animation: slideOut 0.5s ease-in forwards;
          }
        }
      }
    }
    &:has(.model-graph-button.animate) {
      @media (min-width: 1025px) {
        .hero__info-container:not(.mobile) {
          animation: titleOut 0.5s ease-in forwards;
        }
        .hero__model-container:not(.mobile) {
          pointer-events: none;
          animation: titleTo 0.25s ease-in forwards;
          animation-delay: 0.25s;
          pointer-events: all;
        }
        .tabs__panel {
          &.first .swiper-wrapper {
            animation: slideOut 0.5s ease-in forwards;
          }
          &.second .swiper-wrapper {
            opacity: 0;
            animation: slideTo 0.25s ease-in forwards;
            animation-delay: 0.25s;
          }
        }
      }
    }
  }
  &__info-middle {
    display: flex;
    flex-direction: column;
    flex: 1;
    position: relative;
  }
  &__info {
    width: 597px;
    transition: all 0.5s ease;

    @include big-desktop {
      width: 547px;
    }
    @include tablet {
      width: 100%;
      border: unset;
    }
  }

  &__project-upper {
    position: absolute;
    top: 30px;
    left: 80px;
    z-index: 5;
    height: fit-content;
    align-items: center;
    @include tablet {
      left: 30px;
    }
    button {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;

      color: #737373;
    }
    button.active {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;

      color: #fff;
    }
    .logo.mobile {
      display: flex;
      visibility: visible;
    }
  }
  &__project-upper-btns {
    display: flex;
    align-items: center;
    gap: 40px;
    flex: 1;
  }
  &__info {
    border-right: 1px solid #303030;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex: 0 0 auto;
    opacity: 1;
    min-height: 100vh;
    @include big-desktop {
      padding-left: 80px;
      padding-right: 75px;
    }
    @include tablet {
      display: none;
    }
  }
  &__info-container {
    transition: 1s ease-in;
    display: flex;
    flex-direction: column;
    gap: clamp(2.188rem, 1.25rem + 1.04vw, 2.5rem);
    flex: 1;
    max-width: clamp(24.938rem, 17.063rem + 8.75vw, 27.263rem);
    justify-content: center;

    @include big-desktop {
      gap: clamp(0.938rem, -2.139rem + 4.81vw, 2.188rem);
    }
    @media (max-height: 700px) {
      gap: 10px;
    }
    @include tablet {
      display: none;
      padding: 0;
      gap: 40px;
      flex: unset;
    }
    &.mobile {
      gap: 25px;
      display: none;
      @include tablet {
        display: flex;
        gap: 40px;
      }
      @include mobile {
        gap: 25px;
      }
    }
    .hero__title {
      margin-top: -5px;
      @include big-desktop {
        line-height: 66px;
      }
      @media (min-width: 1025px) and (max-height: 700px) {
        font-size: 52px;
        line-height: 100%;
      }
      @include tablet {
        line-height: 104px;
      }
      @include mobile {
        font-size: 50px;
        line-height: 54px;
      }
    }
    @include tablet {
      max-width: 596px;
      margin: 0 auto;
    }
    @include mobile {
      gap: 25px;
    }
  }
  &__model-container {
    display: flex;
    flex-direction: column;
    gap: clamp(2.188rem, 1.25rem + 1.04vw, 2.5rem);
    flex: 1;
    transition: 1s ease-in;
    display: none;
    justify-content: center;
    max-width: clamp(24.938rem, 17.063rem + 8.75vw, 27.263rem);
    opacity: 0;
    pointer-events: none;
    .swiper-nav {
      padding: 0;
    }
    @include big-desktop {
      gap: clamp(0.938rem, -2.139rem + 4.81vw, 2.188rem);
    }
    @media (max-height: 700px) {
      gap: 10px;
    }
    @include tablet {
      display: none;
      padding: 0;
      gap: 40px;
    }
    &.mobile {
      display: none;
      gap: 25px;
      @include tablet {
        display: flex;
        gap: 40px;
        opacity: 1;
        pointer-events: all;
      }
    }
    .hero__title {
      margin-top: -5px;
      span {
        margin-left: 5px;
        display: block;
        @include tablet {
          margin-left: 10px;
        }
        @include mobile {
          margin-left: 5px;
        }
      }
      @include big-desktop {
        font-size: 66px;
        line-height: 66px;
      }
      @media (min-width: 1025px) and (max-height: 700px) {
        font-size: 52px;
        line-height: 100%;
      }
      @include tablet {
        font-size: 104px;
        line-height: 104px;
      }
      @include mobile {
        font-size: 50px;
        line-height: 50px;
      }
    }
    @include tablet {
      max-width: 596px;
      margin: 0 auto;
    }
  }
  // .main__title

  &__title {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: clamp(4.125rem, 2.25rem + 2.08vw, 4.75rem);
    line-height: 76px;
    color: #fff;
    margin: 0;
    margin-left: -5px;

    @include big-desktop {
      font-size: 66px;
    }
    @include tablet {
      font-weight: 600;
      font-size: 104px;

      margin-left: -10px;
    }
    @include mobile {
      margin-left: -5px;
      font-size: 50px;
    }
  }

  // .main__desc

  &__desc {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: clamp(1rem, 0.625rem + 0.42vw, 1.125rem);
    line-height: 28px;
    color: #737373;
    margin: 0;
    @include big-desktop {
      font-size: 16px;
      line-height: 26px;
    }
    @media (min-width: 1025px) and (max-height: 700px) {
      font-size: 14px;
    }
    @include tablet {
      font-size: 18px;
      line-height: 28px;
    }
    @include mobile {
      font-size: 15px;
      line-height: 23px;
    }
  }

  // .main__link

  &__link {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: clamp(1rem, 0.625rem + 0.42vw, 1.125rem);
    text-align: center;
    color: #fff;
    display: flex;
    align-items: center;
    gap: 20px;
    width: fit-content;

    img {
      height: 14px;
      width: 52px;
    }
    @include big-desktop {
      font-size: 16px;
    }
    @include tablet {
      font-size: 18px;
    }
  }
  &__tabs-upper {
    width: 100%;
    padding-top: 42px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all 0.3s ease;
    @include big-desktop {
      padding-top: 40px;
    }
    @include tablet {
      margin-bottom: 100px;
      padding-top: 30px;
    }
    @include mobile {
      margin-bottom: 81px;
      .hero__tabs-upper-links {
        display: none;
      }
    }

    &.mobile {
      display: none;
      @include tablet {
        display: flex;
      }
    }
  }
  &__tabs-upper-links {
    display: flex;
    gap: 25px;
    align-items: center;
    @include tablet {
      img {
        min-width: 20px;
      }
    }
    @include mobile {
      gap: 20px;
      img {
        min-width: 16px;
      }
    }
  }
  &__tabs-upper-link {
    display: flex;
    align-items: center;
    @include tablet {
      &:last-child {
        img {
          min-height: 18px;
        }
      }
    }
  }
  &__tabs {
    min-width: 0;
    @include big-desktop {
      padding-left: 75px;
    }
    @include tablet {
      padding-left: 30px;
    }
    @include mobile {
      padding-left: 20px;
    }
  }
  .tabs__panels {
    position: absolute;
    top: 60px;
    display: flex;
    height: calc(100% - 60px);
    min-width: 100%;
    @include tablet {
      position: static;
      height: 100%;
      display: flex;
      flex-direction: column;
      gap: 100px;
    }
  }
  .tabs__content {
    display: flex;
    .tabs__panel {
      width: 100%;
      pointer-events: none;
      top: 0;
      position: absolute;
      display: flex !important;
      opacity: 0;

      @include tablet {
        pointer-events: all;
        position: relative;
        display: flex !important;
        opacity: 1;
      }

      &--active {
        pointer-events: all;
        display: flex !important;
        opacity: 1;
        top: 0;
        @include tablet {
          display: flex !important;
          .hero__model-container {
            opacity: 1;
            pointer-events: all;
          }
        }
      }
    }
  }
}
.loaded .hero .tabs__content .tabs__panel {
  transition: left 1s, opacity 0.4s ease-in;
}
.hero__info-container:not(.mobile),
.hero__model-container:not(.mobile) {
  animation: none;
}
.tabs__nav {
  transition: all 0.5s ease;
  @include tablet {
    gap: 40px;
  }
}

.info-graph-more {
  margin-right: 30px;
  margin-left: 30px;
  padding-bottom: 20px;
  border-bottom: 1px solid #404040;
  margin-top: -50px;
  display: none;
  @include tablet {
    display: flex;
    margin-right: unset;
    margin-left: unset;
    margin-top: -40px;
  }
  @include mobile {
    margin-top: -30px;
    font-size: 15px;
    gap: 30px;
  }
  align-items: center;
  justify-content: space-between;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 23px;
  color: #fff;
}
