html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  scrollbar-width: none;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  background-color: #101010;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.line-to-right {
  position: relative;
  width: 52px;
  display: block;
  height: 14px;
  &::before {
    content: "";
    position: absolute;
    transform: translateY(-50%);
    width: 100%;
    height: 2px;
    background: #fff;
    top: 50%;
    left: 0;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    background-color: #101010;
    height: 100%;
    border: 2px solid #fff;
    width: 10px;
    height: 10px;
    transform: translateY(-50%) rotate(45deg);
  }
}

.padding-sect {
  padding-left: 80px;
  padding-right: 80px;

  @include tablet {
    padding-left: 30px;
    padding-right: 30px;
  }
  @include mobile {
    padding-left: 20px;
    padding-right: 20px;
  }
}
.tabs {
  position: relative;
  display: flex;
  height: 100%;
  flex-direction: column;
}
.tabs__panel {
  color: #fff;
  @include tablet {
    flex-direction: column;
    gap: 80px;
  }
  @include mobile {
    gap: 50px;
    display: flex;
  }
}

.tabs {
  // .tabs__nav

  &__nav {
    display: flex;
    /*  gap: clamp(1.875rem, 2.08vw, 2.5rem); */
    gap: 40px;
    margin: 0;
    padding: 0;
    flex: 1;
    @include big-desktop {
      gap: clamp(0.938rem, -1.37rem + 3.61vw, 1.875rem);
    }
    @include tablet {
      flex: 1;
    }
    @include mobile {
      opacity: 0;
      pointer-events: none;
    }
  }

  // .tabs__nav-item

  &__nav-item {
    &:first-child a {
      color: #fff;
    }
    & > a {
      display: none;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;
      color: #737373;

      @include tablet {
        display: block;
      }
    }
  }

  // .tabs__nav-btn
  &__panel--active {
    display: flex;
  }
  &__nav-btn {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: clamp(0.875rem, 0.5rem + 0.42vw, 1rem);
    color: #737373;

    &:focus {
      background-color: transparent;
    }
    &:hover {
      color: #fff;
    }
    &--active {
      background: transparent;
      color: #737373;
    }
    @include big-desktop {
      font-size: clamp(0.813rem, 0.659rem + 0.24vw, 0.875rem);
    }
    @include tablet {
      display: none;
      font-size: 16px;
    }
  }

  // .tabs__content

  &__content {
    flex: 1;
    height: 100%;
    @include mobile {
      display: flex;
      flex-direction: column;
      gap: 60px;
    }
  }

  // .tabs__panel

  &__panel {
    height: 100%;
  }
}
