    margin-top: 20px;
  }
}
.swiper-slide {
  background: #1b1b1b;
  /*  height: clamp(39.125rem, 32.75rem + 7.08vw, 41.25rem); */
  /*  height: clamp(495px, 68.5vmin, 660px); */
  /*   max-width: 480px; */
  /*   width: clamp(300px, 51vmin, 480px) !important; */
  /*  width: 495px !important;
  height: 660px; */
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
