.swiper-nav {
  display: flex;
  align-items: center;
  position: relative;
  height: 20px;
  gap: 60px;
  overflow: hidden;
  width: 100%;
  bottom: 74px;
  padding-right: 80px;
  position: static;
  @include tablet {
    position: static;
    margin-top: 40px;
    bottom: unset;
    padding: 0 !important;
    height: auto;
  }
  @include mobile {
    margin-top: 30px;
  }
}

.swiper-nav-buttons {
  position: relative;
  display: flex;
  align-items: center;
  gap: 40px;
  height: 100%;
  width: 62px;

  @include tablet {
    display: none;
  }
}
.swiper-wrapper {
  height: auto;
}
.swiper-button-next,
.swiper-button-prev {
  height: 100% !important;
  width: 11px !important;
  position: static;
  height: fit-content;
  width: fit-content;
  transform: translateY(50%);
}

.swiper-button-next:after,
.swiper-button-prev:after {
  content: "";
  background-image: url(../img/button-arrow-icon.svg);
  font-size: unset;
  background-size: cover;
  width: 11px;
  height: 18px;
  position: absolute;
}
.swiper-button-prev:after {
  transform: rotate(180deg);
}

.swiper-pagination-progressbar {
  background: rgba(0, 0, 0, 0.1);
  background-color: #404040;
  position: static;
  height: 2px !important;
}
.swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
  background-color: #fff;
}
.offset-popup {
  padding: 60px 76px 80px 80px;

  @include tablet {
    padding: 30px;
  }
  @include mobile {
    padding: 20px;
  }
}
.swiper-scrollbar-container {
  width: 100%;
  overflow: hidden;
  position: relative;
  top: -1px;
  @include tablet {
    overflow: unset;
    margin-right: unset;
    top: 0;
  }
}
.icon-more {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 26px;
  gap: 6px;
  min-height: 21px;
  transition: 0.3s ease-in;
  @include mobile {
    min-height: 15px;
    width: 18px;
    gap: 4px;
  }
  .icon-more-lines {
    transition: 0.3s ease-in;
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    position: relative;
    &::after {
      transition: 0.3s ease-in;
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 3px;
      background: #fff;
      @include mobile {
        height: 2px;
      }
    }
    &::before {
      transition: 0.3s ease-in;
      content: "";
      position: absolute;
      top: 0;
      transform: translateY(-50%);
      left: 0;
      width: 100%;
      height: 3px;
      background: #fff;
      @include mobile {
        height: 2px;
      }
    }
    .icon-more-line {
      transition: 0.3s ease-in;
      background: #fff;
      width: 100%;
      height: 3px;
      @include mobile {
        height: 2px;
      }
    }
  }
}
