  }
}

.fancybox__container:not(.is-compact) .fancybox__slide.has-close-btn {
  padding: 36px 30px;
}
.fancybox__container:not(.is-compact) .fancybox__slide:has(.telegram-popup),
.fancybox__container:not(.is-compact) .fancybox__slide:has(.policy-popup),
.fancybox__container:not(.is-compact) .fancybox__slide:has(.offer-popup) {
