  line-height: 125%;
  color: #fff;
  margin: 0;
  margin-bottom: clamp(3.438rem, 2.5rem + 1.04vw, 3.75rem);
  margin-top: -4px;
  @include big-desktop {
    font-size: 38px;
    line-height: 48px;
    margin-bottom: 55px;
  }
  @media (min-width: 1025px) and (max-height: 700px) {
    margin-bottom: 20px;
    font-size: 31px;
    line-height: 40px;
  }
  @include tablet {
    font-size: 40px;
    margin-bottom: 60px;
  }
  @include mobile {
    font-size: 30px;
    margin-bottom: 35px;
  }
}
.input-group {
  margin-bottom: 50px;

  @include big-desktop {
    margin-bottom: 45px;
  }
  @include tablet {
    margin-bottom: 50px;
  }
  @include mobile {
    margin-bottom: 35px;
  }
}
.input-group {
  padding-bottom: 15px;
  border-bottom: 1px solid #404040;

  input {
    width: 100%;
    padding: 0;
    border: none;
    border-radius: 4px;
    background: transparent;
    color: #fff;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;

    &:focus {
      border: unset;
      outline: unset;
    }
    &::placeholder {
      font-weight: 400;
      font-size: 18px;
      line-height: 156%;
      color: #737373;
      @include big-desktop {
        font-size: 16px;
        line-height: 28px;
      }
      @include tablet {
        font-size: 20px;
        line-height: 28px;
      }
      @include mobile {
        font-size: 15px;
      }
    }
    @include big-desktop {
      font-size: 16px;
      line-height: 18px;
    }
    @include tablet {
      font-size: 20px;
      line-height: 28px;
    }
  }

  label {
    font-weight: 600;
    font-size: 20px;
    line-height: 112%;
    color: #fff;
    display: block;
    margin-bottom: 20px;
    @include big-desktop {
      font-size: 18px;
      margin-bottom: 18px;
      line-height: 28px;
    }
    @include tablet {
      font-size: 22px;
      margin-bottom: 25px;
    }
    @include mobile {
      font-size: 17px;
      margin-bottom: 15px;
    }
  }
