.checkbox-item {
  position: relative;
  display: flex;
  align-items: flex-start;
  margin-bottom: 15px;
  cursor: pointer;
  user-select: none;
  margin-bottom: 30px;
  @include tablet {
    margin-bottom: 40px;
  }
  @include mobile {
    margin-bottom: 25px;
  }
}

.checkbox-item input[type="checkbox"] {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  margin-top: 1px;
  position: relative;
  top: 0;
  flex-shrink: 0;
  width: 10px;
  height: 10px;
  margin-right: 15px;
  background-color: #101010;
  border: 2px solid #fff;
  padding: 7px;
  transition: all 0.2s ease;

  @include tablet {
    padding: 9px;
    margin-top: 0;
  }
  @include mobile {
    padding: 7px;
  }
}

.checkmark::after {
  content: "";
  position: absolute;
  opacity: 0;
  left: 50%;
  top: 50%;
  width: 8px;
  height: 8px;
  background-color: #fff;
  transform: translate(-50%, -50%);
  @include tablet {
    width: 10px;
    height: 10px;
  }
}

.checkbox-item input:checked ~ .checkmark:after {
  opacity: 1;
}
.checkbox-item a {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: clamp(0.75rem, 0.375rem + 0.42vw, 0.875rem);
  line-height: 167%;
  color: #fff;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  @include big-desktop {
    font-size: 12px;
    line-height: 183%;
  }
  @include tablet {
    font-size: 16px;
  }
  @include mobile {
    font-size: 12px;
  }
}
.checkbox-item label {
  display: flex;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: clamp(0.75rem, 0.375rem + 0.42vw, 0.875rem);
  line-height: 162%;
  color: #fff;
  cursor: pointer;
  @include big-desktop {
    font-size: 12px;
    line-height: 183%;
    a {
      text-decoration: none;
      @include tablet {
        font-size: 16px;
        line-height: 22px;
        text-decoration: underline;
      }
    }
  }

  @include tablet {
    font-size: 16px;
    line-height: 22px;
  }
  @include mobile {
    max-width: 246px;
    font-size: 12px;
  }
}

.submit-btn {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 18px;
  text-align: center;
  color: #fff;
  border: 2px solid #fff;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 22px 0;
  @include big-desktop {
    font-size: 16px;
  }
  @include tablet {
    font-size: 20px;
    padding: 25px 0;
  }
  @include mobile {
    font-size: 17px;
    padding: 22px 0;
  }
}

.fancybox__content > .f-button.is-close-btn {
  width: 20px;
  height: 20px;
  opacity: 1 !important;
  color: #fff;
  background: transparent;
  @include tablet {
    right: 30px;
    top: 30px;
    width: 24px;
    height: 24px;
  }
  @include mobile {
    right: 20px;
    top: 20px;
  }
}

.policy-popup {
  .is-close-btn {
    top: 30px !important;
    right: 30px !important;
    @include big-desktop {
      width: 16px !important;
      height: 16px !important;
    }
    @include tablet {
      width: 24px !important;
      height: 24px !important;
    }
    @include mobile {
      display: flex;
      right: 20px !important;

      top: 40px !important;
    }
  }
  padding: 0;
  width: 100%;
  max-width: unset;
  ol.main-list {
    list-style-type: none;
    counter-reset: main-item;
    margin: 0;
    padding: 0;
  }
  ol.main-list > li {
    font-size: 18px;
    counter-increment: main-item;
    margin-bottom: 22px;
    @include big-desktop {
      font-size: 16px;
      line-height: 144%;
    }
    @include tablet {
      font-size: 18px;
      margin-bottom: 35px;
    }
  }
  ol.main-list > li:before {
    content: counter(main-item) ". ";
    font-weight: bold;
  }
  ol.sub-list {
    list-style-type: none;
    counter-reset: sub-item;
    margin: 0;
    padding: 0;
  }
  ol.sub-list > li {
    counter-increment: sub-item;
    @include big-desktop {
      font-weight: 400;
      font-size: 16px;
    }
    @include tablet {
      font-size: 18px;
    }
  }
  ol.sub-list > li:before {
    content: counter(main-item) "." counter(sub-item) " ";
  }
  &__content {
    display: grid;
    grid-template-columns: auto 1fr;
    height: 100%;
    @include tablet {
      grid-template-columns: 1fr;
      overflow-y: auto;
    }
  }

  // .policy-popup__title

  &__title {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 52px;
